
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Search from 'vux/src/components/search/index.vue'

export default {
  name: 'app',
  data:function(){
    return {
      page_name:'',
      search_input:'',
      // title:'用户注册',
      header_conf:{
        title:'',
        is_show:true,
        show_back:false
      },
      navbar:{
        data:[
          {
            iconfont:'&#xe6b8;',
            title:'商城',
            url:'/home'
          },{
            iconfont:'&#xe616;',
            title:'分类',
            url:'/list'
          },{
            iconfont:'&#xe694;',
            title:'用户中心',
            url:'/user'
          },{
            iconfont:'&#xe67b;',
            title:'购物车',
            url:'/cart'
          },{
            iconfont:'&#xe657;',
            title:'活动广场',
            url:'/activity'
          },
        ],
        conf:{}
      }
    }
  },
  methods:{
    fetch_data:function(){
      this.page_name=this.$route.name;
      this.xheader_handler(this.$route);
    },
    xheader_handler:function(route){
      var header_conf=this.handler_xheader(route.name);
      this.header_conf=header_conf;
    }
  },
  created:function(){
    //调用header处理
    this.page_name=this.$route.name;
    var header_conf=this.handler_xheader(this.$route.name);
    this.header_conf=header_conf;
  },
  watch:{
    $route:'fetch_data'
  },
  components:{
    XHeader,
    Tabbar,
    TabbarItem,
    Search
  }
}



