export default {
  name:'tree',
  data:function(){
    return {
      open:false,
    }
  },
  computed:{
    isFolder:function(){
      return this.node.children&&this.node.children.length;
    }
  },
  methods:{
    toggle:function(){
      console.log(this);
      document.querySelectAll(".node-item");
      if(this.isFolder){
        this.open=!this.open;
      }
    }
  },
  props:{
    node:Object
  }
}



